/* stylelint-disable */
@mixin small-desktop {
  @media (max-width: (1280px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (1024px)) {
    @content;
  }
}

@mixin small-tablet {
  @media (max-width: (768px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (650px)) {
    @content;
  }
}
