  transition: background-color 0.3s ease-in-out;
}
.resume__timeline::after {
  content: "";
  position: absolute;
  left: 4px;
  top: 0;
  width: 1px;
  height: 100%;
  background-color: var(--icon-fill-muted);
}
.resume__timeline:last-child {
  padding-bottom: 0;
}
@media (any-hover: hover) {
  .resume__timeline:hover::before {
    background-color: var(--accent-color);
  }
  .resume__timeline:hover .resume__date {
    color: var(--accent-color);
  }
}
.resume__date {
  margin-bottom: 40px;
  color: var(--subtext-color);
  transition: color 0.3s ease-in-out;
}
@media (max-width: 1280px) {
  .resume__date {
    margin-bottom: 20px;
