}
@media (max-width: 768px) {
  .resume__position {
    font-size: var(--font-m);
  }
}
.resume__company {
  color: var(--subtext-color);
}
@media (max-width: 768px) {
  .resume__company {
    font-size: var(--font-s);
  }
